<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
  inject: ['reload'],
  data () {
    return {
      columns: [
          {
            type: 'selection',
            width: 40,
            align: 'center',
            // fixed: 'left',
          },
          {
            title: 'ID',
            key: 'id',
            width: 60,
            align: 'center',
            // fixed: 'left'
          },
          {
            title: 'logo',
            key: 'icon_url',
            align: 'center',
            width: 80,
            // fixed: 'left',
            render: (h, params) => {
              return h('div', [
                h('img', {
                  domProps: { // 修改标签默认内容
                    innerHTML: ""
                  },
                  attrs: {
                    src: params.row.icon_url
                  },
                  style: {
                    margin: '5px 5px 0 5px',
                    maxWidth: '50px'
                  },
                  on: {
                    click: () => {
                      this.previewImg(params);
                    }
                  }
                }, 'View')
              ]);
            }
          },
          {
            title: '名称',
            key: 'name',
            align: 'center',
            width: 140,
          },
          {
            title: '关联游戏',
            key: 'fgame',
            width: 140,
            align: 'center',
          },
          {
            title: '类型',
            key: 'type_name',
            align: 'center',
            width: 80,
          },
          {
            title: '原创',
            key: 'original',
            align: 'center',
            width: 60,
            render: (h, params) => {
              const status = params.row.original;
              const color = status == 1 ? '#f75a53' : '#ccc';
              const text = status == 1 ? '原创' : '否';

              return h('div', {
                style: {
                  background: '#fff',
                  border: '1px solid '+color,
                  color: color,
                  borderRadius: '3px',
                  width: '42px',
                  height: '22px',
                  lineHeight: '22px',
                  margin: 'auto'
                },
              }, text);
            }
          },
          {
            title: '添加时间',
            key: 'formatTime',
            width: 120,
            align: 'center',
          },
          {
            title: '合作',
            key: 'cooperation',
            align: 'center',
            width: 60,
            render: (h, params) => {
              const status = params.row.cooperation;
              const color = status == 1 ? '#38b7ea' : '#ccc';
              const text = status == 1 ? '合作' : '否';

              return h('div', {
                style: {
                  background: '#fff',
                  border: '1px solid '+color,
                  color: color,
                  borderRadius: '3px',
                  width: '42px',
                  height: '22px',
                  lineHeight: '22px',
                  margin: 'auto'
                },
              }, text);
            }
          },
          {
            title: '截图',
            key: 'img',
            align: 'center',
            width: 60,
            render: (h, params) => {
              const status = params.row.img;
              const text = status == 1 ? '查看' : '没传';
              if(status == 1) {
                return h('div', [
                  h('Button', {
                    props: {
                      type: 'success',
                      size: 'small',
                    },
                    on: {
                      click: () => {
                        this.$router.push({
                          name: 'appimg',
                          params: {
                            name: params.row.name
                          }
                        })
                      }
                    }
                  }, text)
                ]);
              } else {
                return h('div', [
                  h('div', {
                    style: {
                      background: '#fff',
                      border: '1px solid #ccc',
                      color: '#ccc',
                      borderRadius: '3px',
                      width: '40px',
                      height: '24px',
                      lineHeight: '24px',
                      margin: 'auto'
                    },
                  }, text)
                ]);
              }
            }
          },
          {
            title: '编辑(添加人/最后修改人)',
            key: 'mergeAdmin',
            width: 100,
            align: 'center',
          },
          {
            title: '视频',
            key: 'video',
            align: 'center',
            width: 60,
            render: (h, params) => {
              const status = params.row.video;
              const color = status == '' ? '#ccc' : '#ffbd5b';
              const text = status == '' ? '无' : '视频';

              return h('div', {
                style: {
                  background: '#fff',
                  border: '1px solid '+color,
                  color: color,
                  borderRadius: '3px',
                  width: '42px',
                  height: '22px',
                  lineHeight: '22px',
                  margin: 'auto'
                },
              }, text);
            }
          },
          {
            title: '标签',
            key: 'tagStr',
            align: 'center',
            minWidth: 100,
            maxWidth: 140,
          },
          {
            title: '游戏状态',
            key: 'status_name',
            width: 100,
            align: 'center',
            render: (h, params) => {
              const status = params.row.status;
              const color = status == 1 ? 'success' : status == 2 ? 'info' : status == 3 ? 'warning' : 'error';
              const text = status == 1 ? '已发布' : status == 2 ? '已隐藏' : status == 3 ? '审核中' : '已退回';

              return h('Tag', {
                props: {
                  type: 'dot',
                  color: color,
                }
              }, text);
            }
          },
          {
            title: '状态',
            key: 'state',
            align: 'center',
            width: 100,
            render: (h, params) => {
              const status = params.row.state;
              const color = status == 1 ? 'success' : status == 2 ? 'info' : status == 3 ? 'error' : 'warning';
              const text = status == 1 ? '正常' : status == 2 ? '下架' : status == 3 ? '删除' : '-';

              return h('Tag', {
                props: {
                  type: 'dot',
                  color: color,
                }
              }, text);
            }
          },
          {
            title: '安装包',
            key: 'pack',
            align: 'center',
            width: 100,
            render: (h, params) => {
              if(params.row.pack) {
                const and = params.row.pack.and_url || '';
                const ios = params.row.pack.ios_url || '';
                const status_and = and == '' ? false : true;
                const status_ios = ios == '' ? false : true;
                const text = status_and && status_ios ? 'Android - ios' : 
                            status_and ? 'Android' : 
                            status_ios ?  'ios' : '无';
                return h('div', {}, text);
              }
            }
          },
          {
            title: '操作',
            key: 'action',
            align: 'center',
            // fixed: 'right',
            minWidth: 110,
            render: (h, params) => {
              return h('div', [
                  h('Button', {
                    props: {
                      type: 'info',
                      size: 'small'
                    },
                    style: {
                      margin: '0 4px 2px 0',
                    },
                    on: {
                      click: () =>{
                        this.$router.push({
                          path: '/data/editApp',
                          query: {
                            'appid': params.row.id
                          }
                        })
                      }
                    },
                  }, '编辑'),
                  h('Button', {
                    props: {
                      type: 'warning',
                      size: 'small'
                    },
                    style: {
                      margin: '0 4px 2px 0'
                    }
                  }, 'pc预览'),
                  h('Button', {
                    props: {
                      type: 'info',
                      size: 'small'
                    },
                    style: {
                      margin: '0 4px 2px 0'
                    },
                    on: {
                      click: () =>{
                        this.$router.push({
                          path: '/data/editAppImg',
                          query: {
                            'appid': params.row.id
                          }
                        })
                      }
                    },
                  }, '截图'),
                  h('Button', {
                    props: {
                      type: 'warning',
                      size: 'small'
                    },
                    style: {
                      margin: '0 4px 2px 0'
                    }
                  }, 'm预览')
              ]);
            }
          }
      ],
      data: [],
      datePreSet: {
        shortcuts: [
          {
            text: '现在',
            value () {
              let d = new Date(),
                  str = '';
                  str += d.getFullYear() + '-';
                  str += d.getMonth() + 1 + '-';
                  str += d.getDate() + ' ';
                  str += d.getHours() + ':';
                  str += d.getMinutes() + ':';
                  str += d.getSeconds();
              return str;
            },
            onClick: () => {
              // this.$Message.info('Click today');
            }
          },
          {
            text: '昨天',
            value () {
              const date = new Date();
                    date.setTime(date.getTime() - 3600 * 1000 * 24);
              let str = '';
                  str += date.getFullYear() + '-';
                  str += date.getMonth() + 1 + '-';
                  str += date.getDate() + ' ';
                  str += date.getHours() + ':';
                  str += date.getMinutes() + ':';
                  str += date.getSeconds();
              return str;
            },
            onClick: () => {
              // this.$Message.info('Click yesterday');
            }
          },
          {
            text: '一周前',
            value () {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              let str = '';
                  str += date.getFullYear() + '-';
                  str += date.getMonth() + 1 + '-';
                  str += date.getDate() + ' ';
                  str += date.getHours() + ':';
                  str += date.getMinutes() + ':';
                  str += date.getSeconds();
              return str;
            },
            onClick: () => {
              // this.$Message.info('Click a week ago');
            }
          }
        ]
      },
      formData: {
        seltype: 'name',
        selval: '',
        a_game: '',
        stime: '',
        etime: '',
        tag_name: '',
        is_yuyue: '0',
        apptype: '0',
        recom: '0',
        state: '0',
        original: '0',
        video: '0',
        cooperation: '0',
        url_is: '0',
        status: '0',
        adminid: '0',
      },
      configData: {},
      adminData: {},
      totalPages: 0,
      isLoading: true,
      page: 1,
      per_page: 10,
      addTagId: '',
      tagList: [],
      selectedGameId: '',
      isOpenSelect: false,
      keywordList: [],
      tagnameList: []
    }
  },
  mounted () {
    let _this = this;
    this.handleForm();

    document.onkeydown = function () {// 监听enter事件
      let key = window.event.keyCode;
      if (key === 13) { 
        _this.handleForm()
      }
    }
    // console.log(this.data)
  },
  methods: {
    handleForm (flag) {
      flag ? this.page = 1 : null;
      const token = getToken(),
            page = this.page,
            per_page = this.per_page,
            seltype = this.formData.seltype,
            selval = this.formData.selval,
            stime = this.formData.stime,
            etime = this.formData.etime,
            tag_name = this.formData.tag_name,
            apptype = this.formData.apptype*1,
            recom = this.formData.recom*1,
            original = this.formData.original*1,
            video = this.formData.video*1,
            state = this.formData.state*1,
            status = this.formData.status*1,
            adminid = this.formData.adminid*1,
            is_yuyue = this.formData.is_yuyue*1,
            url_is = this.formData.url_is*1,
            a_game = this.formData.a_game,
            cooperation = this.formData.cooperation*1,
            params = {token, page, per_page, seltype, selval, stime, etime, tag_name, apptype, recom, original, 
            video, state, status, adminid, is_yuyue, url_is, a_game, cooperation};

      // console.log(params)
      if(seltype == 'and_url' || seltype == 'ios_url') {
        if(selval.length < 6) {
          this.$Message.warning('请输入不低于六位包名')
        } else {
          this.getData(params)
        }
      }else {
        this.getData(params)
      }
    },
    getData (params) {
      this.isLoading = true;
      api.appList(params).then(res => {
        this.isLoading = false
        if(res.code == 0) {
          this.totalPages = res.data.totlerows
          this.configData = res.data.config
          this.adminData = res.data.adminlist
          this.data = res.data.applist
          this.data.forEach(ele => {
            //标签调整
            if(ele.tagLists) {
              if (ele.tagLists.length == 1) {
                ele.tagStr = ele.tagLists[0].name;
              } else if(ele.tagLists.length>0){
                ele.tagStr = '';
                ele.tagLists.forEach(itm => {
                  ele.tagStr += (itm.name+' | ')
                });
              } else {
                ele.tagStr = ''
              }
            }
            
            //格式化时间
            ele.formatTime = this.$moment(ele.addtime*1000).format("YYYY-MM-DD k:mm");
            //添加修改人整合
            ele.mergeAdmin = (ele.addadmin_name ? ele.addadmin_name : '') + ' / ' + (ele.upadmin_name ? ele.upadmin_name : '');
          })
        } else {
          this.$Notice.error({
           desc: res.msg
          })
        }
      }).catch(err => {
        this.$Notice.error({
          desc: err
        })
      })
    },
    openTagSelect () {
      if(this.selectedGameId) {
        this.isOpenSelect = true
      } else {
        this.isOpenSelect = false
        this.$Message.warning('请选择要修改标签的游戏')
      }
    },
    getTagData (query) {
      // console.log(query)
      if(query != '') {
        const token = getToken(),
              name = query,
              params = {token, name};
        console.log(params)
        api.getLikeAddAppTagName(params).then(res => {
          if(res.code == 0) {
            this.tagList = res.data
            // console.log(res)
          }else {
            this.$Notice.error({
              desc: res.msg
            })
          }
        })
      }
    },
    selectGame (selection) {
      // console.log(selection)
      this.selectedGameId = '';
      if(selection.length>0) {
        selection.forEach(ele => {
          this.selectedGameId += (','+ele.id)
        })
        // console.log(this.selectedGameId)
      }else {
        this.selectedGameId = ''
      }
    },
    setTagData () {
      if(this.selectedGameId && this.addTagId) {
        this.$Modal.confirm({
          title: '提示',
          content: '<p>确定要修改么？</p>',
          onOk: () => {
            const token = getToken(),
                  tag_check_ids = this.selectedGameId,
                  tag_id = this.addTagId,
                  params = {token, tag_check_ids, tag_id};
            // console.log(params)
            api.updateBeachAppTag(params).then(res => {
              if(res.code == 0) {
                this.selectedGameId = '';
                this.$Notice.success({
                  title: res.msg
                })
                this.handleForm();
              }else {
                this.$Notice.error({
                  title: res.msg
                })
              }
            })
          },
          onCancel: () => {}
        });
      } else if (!this.addTagId) {
        this.$Message.warning('请选择要修改的标签')
      } else {
        this.$Message.warning('请勾选要修改标签的游戏')
      }
    },
    checkedCooperate () {
      const token = getToken(),
            ids = this.selectedGameId,
            type = 2,
            params = {token, type ,ids};
      // console.log(params)
      if(ids) {
        api.batchChangeHz(params).then(res => {
          if(res.code == 0) {
            this.selectedGameId = '';
            this.$Notice.success({
              title: res.msg
            })
            this.handleForm()
          } else {
            this.$Notice.error({
              title: res.msg
            })
          }
        }).catch(err => {
          this.$Notice.error({
            title: err
          })
        })
      } else {
        this.$Message.warning('请勾选要一键合作的游戏！')
      }
      
    },
    autoKeyword (query) {
      if(query != '') {
        const token = getToken(),
            name = query,
            params = {token, name};
         
        api.backApp(params).then(res => {
          if(res.code == 0) {
            this.keywordList = res.data
          }else {
            this.$Notice.error({
              desc: res.msg
            })
          }
        })
      }
    },
    autoTagname (query) {
      if(query != '') {
        const token = getToken(),
            name = query,
            type = 2,
            params = {token, name, type};
         
        api.getLikeTagName(params).then(res => {
          if(res.code == 0) {
            this.tagnameList = res.data
          }else {
            this.$Notice.error({
              desc: res.msg
            })
          }
        })
      }
    },
    downPackLocal () {
      const token = getToken(),
            ids = this.selectedGameId,
            type = 2,
            params = {token, type ,ids};
      console.log(params)
      if(ids) {
        this.selectedGameId = '';
        this.$Notice.success({
          title: '导出成功'
        })
        // api.batchSendDownLoadUrl(params).then(res => {
        //   if(res.code == 0) {
        //     this.$Notice.success({
        //       title: res.msg
        //     })
        //   } else {
        //     this.$Notice.error({
        //       title: res.msg
        //     })
        //   }
        // }).catch(err => {
        //   this.$Notice.error({
        //     title: err
        //   })
        // })
      } else {
        this.$Message.warning('请勾选要一键下载地址到本地的游戏！')
      }

    },
    exportGame () {
      const token = getToken(),
            classify= 2,
            seltype = this.formData.seltype,
            selval = this.formData.selval,
            type = this.formData.apptype,
            params = {token, classify, seltype, selval, type};

      // console.log(params)  
      if(this.formData.apptype != 0) {
        api.exportGameIcon(params).then( res => {
          this.$Notice.success({
            title: '导出成功'
          })
          //数据导出
          const blob = new Blob([res])
          const url = window.URL.createObjectURL(blob)
          const aLink = document.createElement('a')
          aLink.style.display = 'none'
          aLink.href = url
          aLink.setAttribute('download', this.$store.state.itemName+'-应用列表.xls')
          document.body.appendChild(aLink)
          aLink.click()
          document.body.removeChild(aLink) // 下载完成移除元素
          window.URL.revokeObjectURL(url) // 释放掉blob对象
        }).catch(err => {
          this.$Notice.error({
            title: err
          })
        })
      } else {
        this.$Message.warning('类型不能为空')
      }    
    },
    changePage (page) {
      // console.log(page)
      this.page = page;
      this.handleForm();
    },
    changePageNum (num) {
      // console.log(num)
      this.page = 1;
      this.per_page = num;
      this.handleForm();
    },
    getSTime (t) {
      this.formData.stime = t;
    },
    getETime (t) {
      this.formData.etime = t;
    }
  },
  watch: {
    $route () {//检测游戏或应用是否发生修改重新渲染列表
      if(localStorage.getItem('isRenderAppList')) {
        this.handleForm()
        localStorage.setItem('isRenderAppList', false)
      } 
    }
  }
}
</script>
<template>
  <div>
    <h3><Icon type="md-barcode" size=24 />应用列表</h3>
    <div class="functionBtn">
      <Button type="info" style="width:100px" @click="exportGame"><Icon type="md-cloud-download" size=14 />导出应用</Button>
      <Button type="success" ghost @click="downPackLocal"><Icon type="md-arrow-down" />下载包本地</Button>
      <Button type="warning" ghost @click="checkedCooperate"><Icon type="md-checkmark-circle-outline" />一键勾选合作</Button>
      <Button type="info" ghost @click="openTagSelect"><Icon type="ios-color-wand" />一键添加标签</Button>
      <div class="addTagBox" v-show="isOpenSelect">
        <Select clearable v-model="addTagId" filterable :remote-method="getTagData">
          <Option v-for="item,idx in tagList" :value="item.id" :key="idx">{{ item.name }}</Option>
        </Select>
         <Button type="info" ghost @click="setTagData">添加</Button>
      </div>
    </div>
    <div class="formBox">
      <Select v-model="formData.seltype" style="width:100px">
        <Option value="name">名称</Option>
        <Option value="id">ID</Option>
        <Option value="plat">研发商简称</Option>
        <Option value="and_url">安卓包</Option>
        <Option value="ios_url">iOS包</Option>
      </Select>
      <Input v-model="formData.selval" clearable style="width: 200px" />
      <Select clearable v-model="formData.a_game" filterable :remote-method="autoKeyword" placeholder="请在此输入关键字">
        <Option v-for="item,idx in keywordList" :value="item" :key="idx">{{ item }}</Option>
      </Select>
      <DatePicker v-model="formData.stime" @on-change="getSTime" type="datetime" :options="datePreSet" placeholder="选择开始日期" style="width: 200px"></DatePicker>
      <DatePicker v-model="formData.etime" @on-change="getETime" type="datetime" :options="datePreSet" placeholder="选择结束日期" style="width: 200px"></DatePicker>
      <Select clearable v-model="formData.tag_name" filterable :remote-method="autoTagname" placeholder="请在此输入标签名称">
        <Option v-for="item,idx in tagnameList" :value="item" :key="idx">{{ item }}</Option>
      </Select>
    </div>
    <div class="formBox formSelectBox">
      <Select v-model="formData.is_yuyue" style="width:80px" @on-change="handleForm(true)">
        <Option value="0">预约</Option>
        <Option value="1">是</Option>
        <Option value="2">否</Option>
      </Select>
      <Select v-model="formData.apptype" style="width:100px" @on-change="handleForm(true)">
        <Option value="0">应用类型</Option>
        <Option v-for="itm,idx in configData.app_type" :value="idx" :key="idx">{{itm}}</Option>
      </Select>
      <Select v-model="formData.recom" style="width:80px" @on-change="handleForm(true)">
        <Option value="0">推荐</Option>
        <Option v-for="itm,idx in configData.game_recom" :value="idx" :key="idx">{{itm}}</Option>
      </Select>
      <Select v-model="formData.state" style="width:80px" @on-change="handleForm(true)">
        <Option value="0">状态</Option>
        <Option value="1">正常</Option>
        <Option value="2">下架</Option>
        <Option value="3">删除</Option>
      </Select>
      <Select v-model="formData.original" style="width:80px" @on-change="handleForm(true)">
        <Option value="0">原创</Option>
        <Option value="1">是</Option>
        <Option value="2">否</Option>
      </Select>
      <Select v-model="formData.video" style="width:80px" @on-change="handleForm(true)">
        <Option value="0">视频</Option>
        <Option value="1">是</Option>
        <Option value="2">否</Option>
      </Select>
      <Select v-model="formData.cooperation" style="width:80px" @on-change="handleForm(true)">
        <Option value="0">合作</Option>
        <Option value="1">是</Option>
        <Option value="2">否</Option>
      </Select>
      <Select v-model="formData.url_is" style="width:80px" @on-change="handleForm(true)">
        <Option value="0">资源包</Option>
        <Option value="1">有</Option>
        <Option value="2">无</Option>
      </Select>
      <Select v-model="formData.status" style="width:100px" @on-change="handleForm(true)">
        <Option value="0">应用状态</Option>
        <Option v-for="itm,idx in configData.status" :value="idx" :key="idx">{{itm}}</Option>
      </Select>
      <Select v-model="formData.adminid" style="width:100px" @on-change="handleForm(true)">
